import { ReactNode, useRef } from 'react';
import useOutsideTrigger from '../../../hooks/clickedOutsideTrigger/useClickedOutsideTrigger';
import Times from '../../Icons/Times/Times';

const DialogWrapper = ({
  children,
  title,
  onClose,
  size = 'large',
}: {
  children: ReactNode;
  title?: string;
  onClose?: () => void;
  size?: 'large' | 'normal' | '4xlarge';
}) => {
  const wrapperRef = useRef(null);
  useOutsideTrigger(wrapperRef, () => onClose && onClose());

  return (
    <div className="relative z-50" aria-labelledby="modal-title" role="dialog" aria-modal="true">
      <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"></div>
      <div className="fixed inset-0 z-10 overflow-y-auto">
        <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
          <div
            ref={wrapperRef}
            className={`relative transform rounded-lg bg-white text-left shadow-xl transition-all dark:bg-black sm:my-8 sm:w-full ${
              size === 'normal' ? 'sm:max-w-lg' : size === 'large' ? 'sm:max-w-xl' : 'sm:max-w-4xl'
            }`}
          >
            <div className="bg-white px-8 py-8 dark:bg-black dark:text-slate-50">
              {title || onClose ? (
                <div className="-mx-8 -mt-8 mb-8 flex flex-row bg-slate-100 px-8 py-4 dark:bg-slate-700">
                  {title ? (
                    <h3
                      className="my-3 text-2xl font-medium leading-6 text-gray-900 dark:text-slate-50"
                      id="modal-title"
                    >
                      {title}
                    </h3>
                  ) : null}
                  {onClose ? (
                    <button onClick={onClose} className="ml-auto p-2">
                      <Times className="h-5 w-5" />
                    </button>
                  ) : null}
                </div>
              ) : null}
              {children}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DialogWrapper;
